<script>
// 1 - importation tag_commander_wrapper
import TC_Wrapper from "vue-tag-commander";

export default {
  name: "App",
  data() {
    return {
      menuOn: false,
      showMission: false,
      showSupport: false,
      character: {
        name: "Joris/ Flora",
        pronom: "il/ elle",
        metier: "agriculteur(trice)",
        pronomMaj: "Il/ elle"
      },
      oldWrapper: {}
    };
  },
  mounted() {},
  methods: {
    toggleHome() {},
    toggleGuide() {
      const url = window.location.href;
      const lastSlashIndex = url.lastIndexOf("/");
      const path = url.substring(lastSlashIndex + 1);
      const evenement_valeur = path.split("?")[0];
      const wrapper = TC_Wrapper.getInstance();
      wrapper.setTcVars({
        evenement_nom: "clic_le_guide_de_prevention",
        evenement_valeur: evenement_valeur,
        isCATS: "",
        evenement_arbo_niveau2: ""
      });
    },
    toggleMentions() {},
    toggleProtection() {},
    toggleCookies() {},
    togglecookiesGestion() {},
    toggleMenu() {
      if (this.menuOn) {
        this.menuOn = false;
      } else {
        this.menuOn = true;
      }
    },
    toggleMission() {
      if (this.showMission) {
        this.showMission = false;
        this.setOldWrapper();
      } else {
        // ajout variables votre_mission
        //debug tag_commander
        //wrapper.setDebug(true);
        this.getOldWrapper();
        const wrapper = TC_Wrapper.getInstance();
        const url = window.location.href;
        const lastSlashIndex = url.lastIndexOf("/");
        const path = url.substring(lastSlashIndex + 1);
        const evenement_valeur = path.split("?")[0];
        wrapper.setTcVars({
          evenement_nom: "clic_votre_mission",
          evenement_valeur: evenement_valeur,
          isCATS: "",
          evenement_arbo_niveau2: ""
        });

        this.showMission = true;
        this.showSupport = false;
        this.menuOn = false;

        if (this.$store.state.characterChoice == "Joris") {
          this.character = {
            name: this.$store.state.characterChoice,
            pronom: "il",
            metier: "agriculteur",
            pronomMaj: "Il"
          };
        } else if (this.$store.state.characterChoice == "Flora") {
          this.character = {
            name: this.$store.state.characterChoice,
            pronom: "elle",
            metier: "agricultrice",
            pronomMaj: "Elle"
          };
        }
      }
    },
    toggleSupport() {
      if (this.showSupport) {
        this.showSupport = false;
        this.setOldWrapper();
      } else {
        this.getOldWrapper();
        // ajout variables tag_commander_le_credit_vous_accompagne
        const wrapper = TC_Wrapper.getInstance();
        const url = window.location.href;
        const lastSlashIndex = url.lastIndexOf("/");
        const path = url.substring(lastSlashIndex + 1);
        const evenement_valeur = path.split("?")[0];
        wrapper.setTcVars({
          evenement_nom: "clic_le_credit_agricole_vous_accompagne",
          evenement_valeur: evenement_valeur,
          isCATS: "",
          evenement_arbo_niveau2: ""
        });
        this.showSupport = true;
        this.showMission = false;
        this.menuOn = false;
      }
    },
    setOldWrapper() {
      if (this.oldWrapper.page) {
        const wrapper = TC_Wrapper.getInstance();
        wrapper.setTcVars({
          evenement_nom: this.oldWrapper.evenement_nom,
          evenement_valeur: this.oldWrapper.evenement_valeur,
          isCATS: this.oldWrapper.isCATS,
          evenement_arbo_niveau2: this.oldWrapper.evenement_arbo_niveau2
        });
      }
    },
    getOldWrapper() {
      this.oldWrapper.evenement_nom = window.tc_vars["evenement_nom"];
      this.oldWrapper.evenement_valeur = window.tc_vars["evenement_valeur"];
      this.oldWrapper.isCATS = window.tc_vars["isCATS"];
      this.oldWrapper.evenement_arbo_niveau2 =
        window.tc_vars["evenement_arbo_niveau2"];
    }
  }
};
</script>

